.bg-gray {
    background-color: #f6f9fe;
}

.ptb_100 {
    padding: 100px 0;
}

img {
    height: auto;
    max-width: 100%;
    vertical-align: middle;
    border-style: none;
}
.svg-inline--fa {
    display: inline-block;
    font-size: inherit;
    height: 1em;
    vertical-align: -.125em;
}
 
.service-icon span {
    height: 54px;
    width: 54px;
    display: inline-block;
    text-align: center;
    line-height: 54px;
    font-size: 24px;
    color: #7c4fe0;
    border-radius: 4px;
    background-color: #fff;
    border: 1px solid rgba(0, 0, 0, 0.1);
    
    box-shadow: 0 1px 3px rgb(0 0 0 / 10%);
   
    transition: all 0.3s ease 0s;
}
.mt-3 {
    margin-top: 1rem!important;
}
.pr-3 {
    padding-right: 1rem!important;
}
.fa-w-14 {
    width: .875em;
}
.fa-w-16 {
    width: 1em;
}
.fa-w-18 {
    width: 1.125em;
}
p {
    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
    color: #565656;
    margin-bottom: 0;
}
.py-2 {
    padding-bottom: .5rem!important;
    padding-top: .5rem!important;
}
.pl-2, .px-2 {
    padding-left: .5rem!important;
}

.media-body {
   
    flex: 1;
}
 
ol li, ul li {
    list-style: none;
}
.media {
   
    display: flex;
    align-items: flex-start;
}
.list-box .icon {
    color: #764fe0;
}
.align-self-center {
   
    align-self: center!important;
}

svg:not(:root).svg-inline--fa {
    overflow: visible;
}

 
.svg-inline--fa.fa-w-16 {
    width: 1em;
}
 
.svg-inline--fa {
    display: inline-block;
    font-size: inherit;
    height: 1em;
    overflow: visible;
    vertical-align: -.125em;
}



@media (min-width: 576px){

    .pb-sm-0, .py-sm-0 {
        padding-bottom: 0!important;
    }
}