
.footer-fixed {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: -1;
}

@media (min-width: 992px){
    .footer-fixed {
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: -1;
    }

}

.ptb_100 {
    padding: 100px 0;
}
.navbar-brand {
    display: inline-block;
    padding-top: .3125rem;
    padding-bottom: .3125rem;
    margin-right: 1rem;
    font-size: 1.25rem;
    line-height: inherit;
    white-space: nowrap;
    
}
.height-emulator {
    float: left;
    height: 585px;
}
.footer-title{

 
    margin-bottom: .5rem!important;
    font-size: 20px;
    line-height: 1.2;
    font-weight: 600;
    color: #222;
    
}

.button-group {
    margin-top: 30px;
}
.store-buttons a {
    text-align: left;
}

.store-buttons.store-black img {
    max-width: 170px;
    margin-bottom: 10px;
}


 
.d-flex {
   
    display: flex!important;
     
}

.pFooter{
    margin-left: 0 !important;
}
a {
    text-decoration: none;
    outline: none;
    color: #444;
    box-shadow: none;
}
.logo{
   
        height: auto;
        max-width: 100%;
        vertical-align: middle;
        border-style: none;
    
}


.fa-twitter {
    width: .75em;
    margin-left: auto !important;
    margin-right: auto !important;
}
.fa-facebook-f{
    width: .75em;
    margin-left: auto !important;
    margin-right: auto !important;
}
.fa-google-plus-g {
    width: .75em;
    margin-left: auto !important;
    margin-right: auto !important;
}
 
.fa-w-10 {
    width: .625em;
    margin: 0 auto;
}

.social-icons svg {
    display: block;
    height: 100%;
    line-height: 45px;
    transition: all 0.3s ease 0s;
}
 


.footer-area .social-icons > a {
    width: 40px;
    height: 40px;
    font-size: 18px;
}
.social-icons > a.facebook {
    background-color: #3b5999 !important;
}
.social-icons > a.twitter {
    background-color: #55acee !important;
}
.social-icons > a.google-plus {
    background-color: #dd4b39 !important;
}

.social-icons > a {
    margin: 5px;
    color: #fff;
    border-radius: 3px;
    overflow: hidden;
    transition: all 0.3s ease 0s;
}

.copyright-area {
    border-top: 1px solid rgba(0, 0, 0, 0.2);
}