

.ptb_100 {
    padding: 100px 0;
}
section {
    position: relative;
}
.pContact{
    margin-top: 1.5rem!important;

}
.mb-3, .my-3 {
    margin-bottom: 1rem!important;
    margin-left: 40px;
}
 #contact{
    
     padding-bottom: 400px;
     margin-top: -100px;
        
    
 }
.contact-us .social-icon {
    display: inline-block;
    height: 45px;
    width: 45px;
    line-height: 45px;
    font-size: 18px;
    text-align: center;
    background-color: #7c4fe0;
    color: #fff;
    border-radius: 4px;
}
 
.ContactForm{
    font-size: 14px;
    font-weight: 400;
    color: #565656;
    margin-left: 40px;
    
}

.pb-2, .py-2 {
    padding-bottom: .5rem!important;
    padding-top: .5rem!important;
}
ul {
    display: block;
    list-style-type: disc;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    padding-inline-start: 40px;
}
.media {
    
    display: flex;
 
    align-items: flex-start;
}

.mr-3, .mx-3 {
    margin-right: 1rem!important;
}
 
.btnContact{

    width: 100%;
    font-weight: 500;
    color: #fff;
    line-height: 1;
    text-align: center;
    padding: 18px 30px;
    border: 0 none;
    height: 60px;
    border-radius: 8px;
    background: #7c4fe0 !important;
    outline: 0 none;
    position: relative;
    z-index: 1;
}