.ptb_100 {
    padding: 100px 0;
}
.text-white {
    color: #fff!important;
}
.bg-overlay{
    position: relative;
    z-index: 0;
   
}
.avatar-md {
    height: 4rem;
    width: 4rem;
}

.bg-overlay::after, .overlay-dark:after {
    opacity: 0.94;
    z-index: -1;
    position: absolute;
    content: '';
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
}
.overflow-hidden {
    overflow: hidden!important;
}
img {
    height: auto;
    max-width: 100%;
    vertical-align: middle;
    border-style: none;
}

.single-work {
    position: relative;
}
.single-work::after {
    position: absolute;
    content: '\f054';
    font-size: 45px;
    font-weight: 900;
    color: #fff;
    font-family: 'Font Awesome 5 Free';
    top: 50%;
    right: 0;
    margin-right: -50px;
   
}

.text-center {
    text-align: center!important;
}
h3 {
    font-size: 20px;
    line-height: 1.2;
    padding-bottom: 1rem!important;
    padding-top: 1rem!important
}
 
 
.p-3 {
    padding: 1rem!important;
}
 
.test {
    font-size: 40px;
    line-height: 1.3;
}
.work-area{
    background-color:whitesmoke
}